.stat__blocks{
    position: relative;
    padding-bottom: 27px;
    margin-bottom: 14px;
    border-bottom: 1px solid #A1A4B1;
}
.stat__blocks:last-child{
    border-bottom: 0;
}
.stat__blocks__top{
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    a{
        font-weight: bold;
        font-size: 14px;
        line-height: 20px;
        color: #092C9E;
        padding: 6px 20px;
        border: 1px solid #D7D6DC;
        border-radius: 20px;
        display: block;
        margin-left: 18px;
        transition: 0.3s all;
    }
    a:hover{
        box-shadow: 0 0 10px rgba($color: #000000, $alpha: 0.1);
    }
    a.active{
        background-color: #092C9E;
        color: #fff;
    }
}

.stat__detail{
    position: absolute;
    right: 0;
    bottom: 30px;
    a{
        font-weight: bold;
        font-size: 14px;
        line-height: 28px;
        color: #2B5CFB;
        display: flex;
        align-items: center;
        svg{
            margin-left: 10px;
            transition: 0.3s all;
        }
    }
    a:hover svg{
        transform: translateX(5px);
    }
}
.stat__content{
    padding-right: 250px;
}
.stat__list{
    margin: 0;
    padding: 0;
    
    li{
        margin: 0;
        margin-bottom: 24px;
        list-style: none;
    }
}

.stat__list__text{
    font-size: 24px;
    line-height: 36px;
    color: #0A243F;
    span{
        font-weight: bold;
        margin-right: 10px;
    }
}

.stat__chart{
    display: flex;
    margin-top: 30px;
    margin-bottom: 10px;
}
.stat__chart__item{
    width: 169px;
    background: #F1F6FA;
    border-radius: 10px;
    padding: 6px;
    text-align: center;
    font-size: 10px;
    line-height: 24px;
    color: #0A243F;
    padding-top: 0;
    margin-right: 6px;
}
.stat__chart__cifra{
    margin-top: 13px;
    margin-bottom: 14px;
    font-weight: bold;
    font-size: 36px;
    line-height: 24px;
    color: #0A243F;
}
.stat__chart__line{
    background: #D7D6DC;
    border-radius: 2px;
    height: 4px;
    position: relative;
    overflow: hidden;
}

.stat__chart__line2{
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #092C9E;
}